@import '../helpers/variables';
@import '../helpers/mixins';

.footer_social {
  display: flex;
  align-items: center;
  .social {
    display: block;
    width: 42px;
    height: 42px;
    margin: 0 8px;
    border-radius: 50%;
    &:hover {
      -webkit-box-shadow: 0 0 10px $yellow;
      box-shadow: 0 0 10px $yellow;
    }
    &.linkedin {
      background: url(image-url('/icons/social/linkedin.svg')) center no-repeat;
      background-size: contain;
    }
    &.facebook {
      background: url(image-url('/icons/social/facebook.svg')) center no-repeat;
      background-size: contain;
    }
    &.youtube {
      background: url(image-url('/icons/social/youtube.svg')) center no-repeat;
      background-size: contain;
    }
    &.twitter {
      background: url(image-url('/icons/social/twitter.svg')) center no-repeat;
      background-size: contain;
    }
    &.instagram {
      background: url(image-url('/icons/social/instagram.svg')) center no-repeat;
      background-size: contain;
    }
  }

}

.social_share {
  display: flex;
  flex-direction: column;
  width: 104px;
  min-width: 104px;
  padding-top: 56px;
  .social_share_box {
    position: sticky;
    top: 150px;
    .hex {
      &:hover {
        background: url(image-url('/icons/hex_hover_color.svg')) center no-repeat;
      }
    }
  }
}

